<template>
  <div class="header-inner">
    <a
      class="logo"
      href="#"
      @click="goLandingPage"
    >
      <img
        :src="agent.logo"
        :alt="agent.name"
      />
    </a>
    <jsk-topnav
      :style="{
        marginLeft: '12px',
        color: 'white'
      }"
      :topnav-items="navItems"
      :topnav-init-active="navActive"
      topnav-active-color="#FFFFFF"
      ref="navigation"
    />
    <div class="right">
      <div
        class="user"
        v-if="isLoggedin"
      >
        <jsk-dropdown dropdown-trigger="click">
          <span class="el-dropdown-link">{{ name }}
            <i class="el-icon-arrow-down sk-icon" />
          </span>
            <jsk-dropdown-menu slot="dropdown">
              <jsk-dropdown-item @click.native="dialogNameVisible = true">修改昵称</jsk-dropdown-item>
              <jsk-dialog
                custom-class="dialog-custom"
                :visible.sync="dialogNameVisible"
                dialog-title="更改昵称"
                dialog-width="400px"
                is-on-body
                :before-close="resetName">
                  <jsk-form
                    :form-model="paramsName"
                    :form-rules="rules"
                    ref="paramsName"
                  >
                    <jsk-input v-model="paramsName.name" placeholder="请输入新昵称..." />
                  </jsk-form>
                  <span slot="footer">
                    <jsk-button @click="changeName">确认</jsk-button>
                    <jsk-button @click="resetName">取消</jsk-button>
                  </span>
              </jsk-dialog>
              <jsk-dropdown-item @click.native="dialogPasswordVisible = true">修改密码</jsk-dropdown-item>
              <jsk-dialog
                custom-class="dialog-custom"
                :visible.sync="dialogPasswordVisible"
                dialog-title="更改密码"
                dialog-width="400px"
                is-on-body
                :before-close="resetPassword">
                  <jsk-form
                    class="reset-password-form"
                    form-label-position="top"
                    :form-model="paramsPassword"
                    :form-rules="rules"
                    ref="paramsPassword">
                      <jsk-form-item
                        form-item-label="旧密码"
                        form-item-prop="oldPassword"
                      >
                        <jsk-input
                          type="password"
                          v-model="paramsPassword.oldPassword"
                          placeholder="请输入旧密码..."
                        />
                      </jsk-form-item>
                      <jsk-form-item
                        form-item-label="新密码"
                        form-item-prop="newPassword"
                      >
                        <jsk-input
                          type="password"
                          v-model="paramsPassword.newPassword"
                          placeholder="请设置一个新密码..."
                        />
                      </jsk-form-item>
                      <jsk-form-item
                        form-item-label="确认新密码"
                        form-item-prop="checkPassword"
                      >
                        <jsk-input
                          type="password"
                          v-model="paramsPassword.checkPassword"
                          placeholder="请再次输入新密码..."
                        />
                      </jsk-form-item>
                  </jsk-form>
                  <span slot="footer">
                    <jsk-button @click="changePassword">确认</jsk-button>
                    <jsk-button @click="resetPassword">取消</jsk-button>
                  </span>
                </jsk-dialog>
            </jsk-dropdown-menu>
        </jsk-dropdown>
        <jsk-divider divider-direction="vertical"></jsk-divider>
        <a href="#" @click="logout">登出</a>
      </div>
    </div>
  </div>
</template>
<script>
import {
  Topnav,
  Divider,
  Notification,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  Form,
  FormItem,
  Input,
  Dialog,
  Button
} from 'jellies'
export default {
  name: 'SkHeader',
  inject: ['agent'],
  components: {
    JskTopnav: Topnav,
    JskDivider: Divider,
    JskDropdown: Dropdown,
    JskDropdownMenu: DropdownMenu,
    JskDropdownItem: DropdownItem,
    JskForm: Form,
    JskFormItem: FormItem,
    JskInput: Input,
    JskDialog: Dialog,
    JskButton: Button
  },
  data: function () {
    return {
      studentsNavItems: [
        {
          name: '课程学习',
          callback: () => {
            this.$router.push('/courses')
          }
        }
      ],
      teachersNavItems: [
        {
          name: '课程学习',
          callback: () => {
            this.$router.push('/courses')
          }
        },
        {
          name: '教学管理',
          callback: () => {
            this.$router.push('/courses/management')
          }
        }
      ],
      dialogNameVisible: false,
      dialogPasswordVisible: false,
      paramsName: {
        name: ''
      },
      paramsPassword: {
        oldPassword: '',
        newPassword: '',
        checkPassword: ''
      },
      rules: {
        name: [
          { min: 1, max: 8, message: '用户名的长度应小于或等于8。', trigger: 'blur' }
        ],
        oldPassword: [
          { required: true, message: '请输入旧密码', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '请设置新密码', trigger: 'blur' },
          { min: 6, max: 24, message: '密码非法。长度应在6到24之间。', trigger: 'blur' }
        ],
        checkPassword: [
          { required: true, message: '请再次输入新密码', trigger: 'blur' },
          { min: 6, max: 24, message: '密码非法。长度应在6到24之间。', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    goLandingPage: function () {
      this.$router.push('/')
    },
    logout: function () {
      this.$store.dispatch('logout').then(
        () => {
          this.$router.push('/')
        }
      ).catch(
        err => {
          Notification({
            title: 'Error',
            message: err.data,
            duration: 3000,
            showClose: false
          })
        }
      )
    },
    resetName () {
      if (this.$refs['paramsName'] !== undefined) {
        this.$refs['paramsName'].resetFields()
        this.dialogNameVisible = false
      }
    },
    resetPassword () {
      if (this.$refs['paramsPassword'] !== undefined) {
        this.$refs['paramsPassword'].resetFields()
        this.dialogPasswordVisible = false
      }
    },
    changeName () {
      this.$refs['paramsName'].validate((vaild) => {
        if (!vaild) {
          return false
        }
        if (this.paramsName.name !== this.name) {
          this.$axios.post('/suanke/user/update-name-and-reset-password', this.paramsName).then((res) => {
            if (res.data.status !== 'success') {
              Notification({
                title: 'Error',
                message: res.data.message,
                duration: 3000,
                type: 'error',
                showClose: false
              })
              return
            }
            this.dialogNameVisible = false
            this.$store.getters.userInfo.name = this.paramsName.name
            Notification({
              title: '成功',
              message: '信息更新成功',
              duration: 3000,
              type: 'success',
              showClose: false
            })
          })
          return
        }
        Notification({
          title: '错误',
          message: '请设置一个昵称',
          duration: 3000,
          type: 'error',
          showClose: false
        })
      })
    },
    changePassword () {
      this.$refs['paramsPassword'].validate((vaild) => {
        if (!vaild) {
          return false
        }
        if (this.paramsPassword.newPassword === this.paramsPassword.oldPassword) {
          Notification({
            title: '错误',
            message: '密码未变化，请重新设置',
            duration: 3000,
            type: 'error',
            showClose: false
          })
          return
        }
        if (this.paramsPassword.checkPassword !== this.paramsPassword.newPassword) {
          Notification({
            title: '错误',
            message: '你两次输入了不同的密码，请检查后重试',
            duration: 3000,
            type: 'error',
            showClose: false
          })
          return
        }
        this.$axios.post('/suanke/user/update-name-and-reset-password', {
          password: this.paramsPassword.newPassword,
          old_password: this.paramsPassword.oldPassword}).then(({data}) => {
          if (data.status !== 'success') {
            Notification({
              title: 'Error',
              message: data.message,
              duration: 3000,
              type: 'error',
              showClose: false
            })
            return
          }
          this.resetPassword()
          Notification({
            title: '成功',
            message: '信息更新成功',
            duration: 3000,
            type: 'success',
            showClose: false
          })
          localStorage.removeItem('userInfo')
          localStorage.removeItem('redirectUrl')
          localStorage.removeItem('isLoggedIn')
          this.$store.commit('logout')
          this.$router.push({
            path: '/'
          })
        })
      })
    }
  },
  computed: {
    isLoggedin: function () {
      return this.$store.getters.isLoggedIn
    },
    name: function () {
      if (this.$store.getters.userInfo) {
        return this.$store.getters.userInfo.name
      }
    },
    username: function () {
      if (this.$store.getters.userInfo) {
        return this.$store.getters.userInfo.name
      }
      return ''
    },
    navItems: function () {
      if (this.$store.getters.userInfo) {
        if (this.$store.getters.userInfo.role === 'student') {
          return this.studentsNavItems
        } else {
          return this.teachersNavItems
        }
      }
      return []
    },
    navActive: function () {
      if (this.$route.path === '/courses') {
        return 0
      } else if (this.$route.path === '/courses/management') {
        return 1
      }
      return -1
    }
  },
  watch: {
    navActive: function (currentActive) {
      this.$refs.navigation.currentActive = currentActive
      this.$refs.navigation.currentHover = currentActive
    }
  }
}
</script>
<style lang="scss" scoped>
.header-inner {
  width: 100%;
  display: flex;
  .logo {
    margin: 10px;
    overflow: hidden;
    display: block;
    height: 27px;
    width: 155px;
    & > img {
      height: 27px;
    }
  }
  .right {
    text-align: right;
    flex-grow: 1;
    color: #FFFFFF;
    line-height: 50px;
    padding-right: 12px;
    a {
      color: #FFFFFF;
    }
    .el-dropdown {
      color: $font-fff;
    }
    .el-dropdown-link {
      cursor: pointer;
      .sk-icon {
        height: 16px;
        padding-left: 4px;
        vertical-align: middle;
      }
    }
  }
  .reset-password-form {
    &> div:last-child {
      margin-bottom: 0;
    }
    label {
      padding: 0 !important;
    }
  }
}
</style>



