import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

const $axios = axios.create({
  baseURL: process.env.API_ROOT,
  timeout: 30000,
  withCredentials: true
})

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    status: '',
    redirectUrl: localStorage.getItem('redirectUrl') || '',
    isLoggedIn: localStorage.getItem('isLoggedIn') || false,
    userInfo: JSON.parse(localStorage.getItem('userInfo')) || ''
  },
  mutations: {
    auth_request (state) {
      state.status = 'requesting'
    },
    auth_success (state, [redirectUrl]) {
      state.status = 'success'
      state.redirectUrl = redirectUrl
      state.isLoggedIn = true
    },
    auth_error (state) {
      state.status = 'error'
    },
    logout (state) {
      state.status = ''
      state.redirectUrl = ''
      state.isLoggedIn = false
      state.userInfo = ''
    },
    user_info_request (state) {
      state.status = 'requesting'
    },
    user_info_success (state, userInfo) {
      state.status = 'success'
      state.userInfo = userInfo
    },
    user_info_error (state) {
      state.status = 'error'
    }
  },
  actions: {
    login: function ({commit}, user) {
      return new Promise((resolve, reject) => {
        commit('auth_request')
        $axios.post(
          '/suanke/auth/login',
          user
        ).then(response => {
          const responseBody = response.data
          if (responseBody.status === 'success') {
            const redirectUrl = responseBody.data.redirect_url
            localStorage.setItem('isLoggedIn', true)
            localStorage.setItem('redirectUrl', redirectUrl)
            commit('auth_success', [redirectUrl])
            resolve(responseBody)
          } else {
            commit('auth_error')
            localStorage.removeItem('isLoggedIn')
            localStorage.removeItem('redirectUrl')
            reject(responseBody)
          }
        })
      })
    },
    getAgent: function ({commit}, prefix) {
      return new Promise((resolve, reject) => {
        commit('agent_request')
        $axios.get('/suanke/api/agent/info', {
          params: {
            prefix: prefix
          }
        }).then(response => {
          const responseBody = response.data
          if (responseBody.status === 'success') {
            const agentInfo = responseBody.data
            commit('agent_info_success', agentInfo)
            resolve(responseBody)
          } else {
            commit('agent_info_error')
            reject(responseBody)
          }
        }).catch(response => {
          const responseBody = response.data
          commit('agent_info_error')
          reject(responseBody)
        })
      })
    },
    getUser: function ({commit}) {
      return new Promise((resolve, reject) => {
        commit('user_info_request')
        $axios.get('/suanke/api/user/info').then(response => {
          const responseBody = response.data
          if (responseBody.status === 'success') {
            const userInfo = responseBody.data
            localStorage.setItem('userInfo', JSON.stringify(userInfo))
            commit('user_info_success', userInfo)
            resolve(responseBody)
          } else {
            commit('user_info_error')
            commit('logout')
            localStorage.removeItem('userInfo')
            localStorage.removeItem('redirectUrl')
            localStorage.removeItem('isLoggedIn')
            reject(responseBody)
          }
        })
      })
    },
    logout: function ({commit}) {
      return new Promise((resolve, reject) => {
        $axios.get('/suanke/auth/logout').then(response => {
          const responseBody = response.data
          if (responseBody.status === 'success') {
            commit('logout')
            localStorage.removeItem('userInfo')
            localStorage.removeItem('redirectUrl')
            localStorage.removeItem('isLoggedIn')
            resolve(responseBody)
          } else {
            reject(responseBody)
          }
        })
      })
    }
  },
  getters: {
    redirectUrl: state => state.redirectUrl,
    isLoggedIn: state => state.isLoggedIn,
    userInfo: state => state.userInfo,
    authStatus: state => state.status
  }
})



