<template>
  <div class="loading" v-loading="true">
    <iframe
      :src="redirectUrl"
      frameborder="0"
      id="iframe-content"
      allow="microphone;camera;midi;encrypted-media;"
    />
  </div>
</template>
<script>
import {
  Notification
} from 'jellies'
export default {
  name: 'Loading',
  computed: {
    redirectUrl: function () {
      return this.$store.getters.redirectUrl
    }
  },
  mounted () {
    window.addEventListener('message', ({ data }) => {
      if (data === 'success') {
        this.$store.dispatch('getUser').then(
          () => {
            this.$router.push('/courses')
          }
        ).catch(
          err => {
            Notification({
              title: '错误',
              message: err.data,
              duration: 3000,
              showClose: false
            })
            this.$router.push({
              path: '/'
            })
          }
        )
      } else if (data === 'fail' || data === 'error') {
        Notification({
          title: '错误',
          message: '身份验证出错，请重新登录。',
          duration: 3000,
          showClose: false
        })
        this.$router.push({
          path: '/'
        })
      }
    })
  }
}
</script>
<style lang="scss" scoped>
.loading {
  width: 100%;
  height: calc(100vh - 40px);
}
</style>



