<template>
  <div class="student-management">
    <iframe :src="studentManagementUrl" frameborder="0" allow="microphone;camera;midi;encrypted-media;"></iframe>
  </div>
</template>
<script>
export default {
  name: 'StudentManagement',
  inject: ['agent'],
  mounted: function () {
    document.title = `学生管理 - 课程管理 - ${this.agent.name}`
  },
  computed: {
    courseId: function () {
      return this.$route.params['course_id']
    },
    studentManagementUrl: function () {
      let baseUrl = this.$baseUrl
      return `${baseUrl}/admin/instructor/students/${this.courseId}`
    }
  }
}
</script>
<style lang="scss" scopes>
.student-management {
  height: 100vh;
  iframe {
    width: 100%;
    height: 100vh;
  }
}
</style>



