<template>
  <div class="courses" v-loading="isLoading">
    <div class="empty" v-if="!isLoading && !hasCourse">
      课程列表为空
    </div>
    <jsk-container
      v-else-if="!isLoading"
      is-centered
      class="courses-container"
    >
      <jsk-row :row-gutter="24">
        <jsk-col :col-span="12" v-for="course in courses" :key="course.id">
          <jsk-panel class="course-panel">
            <h3 class="course-title">{{ course.title }}</h3>
            <div>
              <jsk-button
                button-type="success"
                @click.native="toCourseManagement(course.course_id)"
              >
                授课管理
              </jsk-button>
              <jsk-button
                button-type="success"
                @click.native="toStudentManagement(course.course_id)"
              >
                学生管理
              </jsk-button>
            </div>
          </jsk-panel>
        </jsk-col>
      </jsk-row>
      <jsk-pagination
        pagination-layout="prev, pager, next"
        :pagination-total="totalCourses"
        :pagination-page-size="pageSize"
        :pagination-current-page.sync="currentPage"
        @current-change="handleCurrentChange"
      />
    </jsk-container>
  </div>
</template>
<script>
import {
  Panel,
  Container,
  Row,
  Col,
  Progress,
  Button,
  Pagination
} from 'jellies'
export default {
  name: 'CoursesManagement',
  components: {
    JskPanel: Panel,
    JskContainer: Container,
    JskRow: Row,
    JskCol: Col,
    JskProgress: Progress,
    JskButton: Button,
    JskPagination: Pagination
  },
  inject: ['agent'],
  data: function () {
    return {
      courses: [],
      pageSize: 50,
      totalCourses: 0,
      currentPage: 1,
      isLoading: false
    }
  },
  mounted: function () {
    document.title = `课程管理 - ${this.agent.name}`
  },
  created () {
    this.requestCourses()
  },
  computed: {
    hasCourse: function () {
      return this.courses.length > 0
    }
  },
  methods: {
    requestCourses() {
      this.isLoading = true
      this.$axios.post('/suanke/api/teacher_courses', {
        page: this.currentPage
      }).then(response => {
        const responseBody = response.data
        if (responseBody.status === 'success') {
          const { courses, total } = responseBody.data
          this.courses = courses
          this.totalCourses = total
        } else {
          Notification({
            title: '错误',
            message: '网络错误，请刷新页面重试',
            duration: 3000,
            showClose: false
          })
        }
        this.isLoading = false
      })
    },
    toCourseManagement (courseId) {
      this.$router.push({
        path: `/course/management/${courseId}`
      })
    },
    toStudentManagement (courseId) {
      this.$router.push({
        path: `/course/management/${courseId}/students`
      })
    },
    handleCurrentChange(page) {
      this.currentPage = page
      this.requestCourses()
    }
  }
}
</script>
<style lang="scss" scoped>
.courses {
  width: 100%;
  min-height: calc(100vh - 90px);
  background: $back-color;
  padding-top: 50px;
  .empty {
    padding-top: 200px;
    font-size: 36px;
    line-height: 57px;
    font-weight: 700;
    color: $font-tip;
    text-align: center;
  }
  &-container {
    margin-top: 40px;
    .el-pagination {
      text-align: center;
      margin-bottom: 24px;
    }
  }
}
.course-panel {
  cursor: pointer;
  margin-bottom: 24px;
  h3 {
    font-size: 20px;
    margin-bottom: 12px;
  }
  .course-title {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
  }
}
</style>



