<template>
  <div>
    <div class="mtr-hero">
      <jsk-container class="mtr-hero-container" is-centered>
        <header>
          <div class="mtr-hero-logo-container">
            <a class="mtr-hero-logo">
              <img :src="agent.logo"/>
            </a>
          </div>
          <div class="mtr-hero-nav">
          </div>
        </header>
      </jsk-container>
      <div class="mtr-hero-ribbon">
        <img src="../assets/top-ribbon.png">
      </div>
    </div>
    <main class="mtr-main">
      <jsk-container class="mtr-main-container" is-centered>
        <passport :username-prefix="agent.prefix"/>
      </jsk-container>
    </main>
    <footer class="mtr-footer">
      <div>
        &copy;{{ dayjs().format('YYYY') }} {{ agent.name }} <br>
        <span>京ICP备14048711号-10</span>
      </div>
    </footer>
  </div>
</template>

<script>
import {
  Container,
  Button,
  Row,
  Col
} from 'jellies'
import Passport from './Passport'
export default {
  name: 'Landing',
  inject: ['agent'],
  components: {
    JskContainer: Container,
    JskButton: Button,
    JskRow: Row,
    JskCol: Col,
    Passport
  },
  created: function () {
    if (this.$store.getters.isLoggedIn) {
      this.$router.push('/courses')
    }
  },
  mounted: function () {
    document.title = `首页 - ${this.agent.name}`
  },
  data () {
    return {
      dayjs: require('dayjs')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
body {
  background: #F4F5F8
}
.mtr-hero {
  height: 100px;
  position: relative;
  background: rgb(65,177,70);
  background: linear-gradient(0deg, rgba(65,177,70,1) 0%, rgba(108,204,61,1) 100%);
  margin-bottom: 38px;
  header {
    display: flex;
    z-index: 2000;
  }
  &-container {
    height: 100px;
    width: 100%;
    position: relative;
  }
  &-logo {
    overflow: hidden;
    display: block;
    height: 27px;
    width: 155px;
    margin: 0 auto;
    text-align: center;
    &-container {
      padding-top: 14px;
      width: 220px;
      height: 60px;
      background-color: #41B146;
      box-sizing: border-box;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;
      flex-grow: 0;
    }
    & > img {
      height: 27px;
    }
  }
  &-nav {
    height: 60px;
    line-height: 60px;
    flex-grow: 1;
    text-align: right;
  }

  &-heading {
    margin-top: 75px;
    width: 615px;
    height: 240px;
  }
  &-ribbon {
    position: absolute;
    bottom: -20px;
    width: 100%;
    height: 50px;
    z-index: 1500;
    img {
      height: 100%;
      width: 100%;
    }
  }
}
.mtr-main {
  width: 100%;
  &-container {
    h3 {
      font-size: 32px;
      // font-family: 'Sansita Swashed', cursive;
      color: #555555;
      margin-top: 20px;
    }
    p {
      font-size: 18px;
      color: #6D6D6D
    }
    section {
      z-index: 1000;
      margin-bottom: 80px;
    }
  }
}
.mtr-footer {
  background-color: #4CB844;
  background: linear-gradient(0deg, rgba(65,177,70,1) 0%, rgba(108,204,61,1) 100%);
  text-align: center;
  line-height: 20px;
  font-size: 16px;
  color: #FFF;
  height: 80px;
  padding: 1px;
  div {
    margin-top: 19px;
  }
  span {
    font-size: 14px;
  }
}
</style>



