<template>
  <div class="passport">
    <jsk-panel class="passport-panel">
      <jsk-form
        form-label-position="top"
        :form-label-width="85"
        :form-model="form"
        :form-rules="rules"
        ref="form"
        class="login-form"
        @submit.native.prevent
      >
        <jsk-form-item
          form-item-prop="username"
          form-item-label="用户名"
        >
          <jsk-input v-model="form.username" placeholder="请输入你的用户名"/>
        </jsk-form-item>
        <jsk-form-item
          form-item-prop="password"
          form-item-label="密码"
        >
          <jsk-input v-model="form.password" type="password" placeholder="请输入你的密码"/>
        </jsk-form-item>
        <jsk-form-item>
          <jsk-button class="login-form-button" @click="login">登录开始学习</jsk-button>
        </jsk-form-item>
      </jsk-form>
    </jsk-panel>
  </div>
</template>
<script>
import {
  Form,
  FormItem,
  Input,
  Button,
  Notification,
  Panel
} from 'jellies'
export default {
  name: 'Login',
  props: {
    usernamePrefix: {
      type: String,
      default: ':'
    }
  },
  data: function () {
    return {
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [{
          required: true,
          message: '请输入用户名',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '请输入密码',
          trigger: 'blur'
        }]
      },
      redirectUrl: ''
    }
  },
  computed: {
    username: function () {
      const formUsername = this.form.username
      if (formUsername.indexOf(':') === -1) {
        return this.usernamePrefix + ':' + formUsername
      }
      return formUsername
    }
  },
  components: {
    JskForm: Form,
    JskFormItem: FormItem,
    JskInput: Input,
    JskButton: Button,
    JskPanel: Panel
  },
  created: function () {
    if (this.$store.getters.isLoggedIn) {
      this.$router.push('/courses')
    }
  },
  methods: {
    login () {
      this.$refs['form'].validate((vaild) => {
        if (vaild) {
          this.$store.dispatch('login', {
            username: this.username,
            password: this.form.password
          }).then(
            () => this.$router.push('/loading')
          ).catch(
            err => {
              Notification({
                title: 'Error',
                message: err.data.message,
                duration: 3000,
                showClose: false
              })
            }
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.passport {
  width: 100%;
  height: calc(100vh - 180px);
  overflow: hidden;
  z-index: 2000;
  margin-top: -40px;
  display: flex;
  &-panel {
    width: 450px;
    margin: auto;
    justify-items: center;
  }
}
.login-form {
  margin-bottom: -22px;
  overflow: hidden;
  &-forget {
    margin-top: -12px;
    text-align: right;
  }
  &-button {
    width: 100%;
    background-color: $main-color;
    border-color: $main-color;
    &:hover {
      background-color: lighten($main-color, 5%);
      border-color: lighten($main-color, 5%);
    }
  }
}
</style>
<style lang="scss">
.passport-panel {
  .el-form-item__label {
    padding: 0 !important;
  }
}
</style>



