<template>
  <div class="course">
    <jsk-header class="header" header-center-width="100%" header-background-color="#4CB844">
      <sk-header />
    </jsk-header>
    <iframe :src="courseUrl" frameborder="0" allowfullscreen="true" webkitallowfullscreen="true" mozallowfullscreen="true" allow="microphone;camera;midi;encrypted-media;"></iframe>
  </div>
</template>
<script>
import { Header } from 'jellies'
import SkHeader from '../components/Header'
export default {
  name: 'Course',
  inject: ['agent'],
  components: {
    SkHeader,
    JskHeader: Header
  },
  mounted: function () {
    document.title = `课程学习 - ${this.agent.name}`
  },
  computed: {
    courseId: function () {
      return this.$route.params['course_id']
    },
    courseUrl: function () {
      return `${this.$baseUrl}/course/${this.courseId}`
    }
  },
  methods: {
    goCoursesPage: function () {
      this.$router.push('/courses')
    }
  }
}
</script>
<style lang="scss" scopes>
.course {
  background: #FFFFFF;
  position: relative;
  iframe {
    width: 100%;
    height: 100vh;
    display: block;
  }
}
.header {
  position: fixed;
  top: 0;
  z-index: 100;
}
</style>



