<template>
  <div class="course-management">
    <iframe :src="courseManagementUrl" frameborder="0" allow="microphone;camera;midi;encrypted-media;"></iframe>
  </div>
</template>
<script>
export default {
  name: 'CourseManagement',
  inject: ['agent'],
  mounted: function () {
    document.title = `授课管理 - 课程管理 - ${this.agent.name}`
  },
  computed: {
    courseId: function () {
      return this.$route.params['course_id']
    },
    courseManagementUrl: function () {
      let baseUrl = this.$baseUrl
      return `${baseUrl}/admin/instructor/${this.courseId}`
    }
  }
}
</script>
<style lang="scss" scopes>
.course-management {
  height: 100vh;
  iframe {
    width: 100%;
    height: 100vh;
  }
}
</style>



