<template>
  <div class="courses" v-loading="isLoading">
    <div class="empty" v-if="!isLoading && !hasCourse">
      你的课程列表为空<br>
      请联系你的老师加入课程
    </div>
    <jsk-container
      v-else-if="!isLoading"
      is-centered
      class="courses-container"
    >
      <jsk-row :row-gutter="24">
        <jsk-col :col-span="12" v-for="course in courses" :key="course.id">
          <jsk-panel class="course-panel" @click.native="toCourse(course.url)">
            <h3 class="course-title">{{ course.title }}</h3>
            <div class="comments">
              <div>上次学到：{{ course.last_learn_lesson.title }}</div>
              <div class="precentage">{{ course.proportion }}%</div>
            </div>
            <jsk-progress
              :progress-percentage="parseInt(course.proportion)"
              :progress-stroke-width="12"
              progress-bar-color="#4CB844"
            />
          </jsk-panel>
        </jsk-col>
      </jsk-row>
    </jsk-container>
  </div>
</template>
<script>
import {
  Panel,
  Container,
  Row,
  Col,
  Progress,
  Notification
} from 'jellies'
export default {
  name: 'Courses',
  inject: ['agent'],
  data: function () {
    return {
      courses: [],
      isLoading: false
    }
  },
  components: {
    JskPanel: Panel,
    JskContainer: Container,
    JskRow: Row,
    JskCol: Col,
    JskProgress: Progress
  },
  created () {
    this.isLoading = true
    this.$axios.post('/suanke/api/user_courses').then(response => {
      const responseBody = response.data
      if (responseBody.status === 'success') {
        this.courses = responseBody.data
      } else {
        Notification({
          title: '错误',
          message: '网络错误，请刷新页面重试',
          duration: 3000,
          showClose: false
        })
      }
      this.isLoading = false
    })
  },
  mounted: function () {
    document.title = `课程列表 - ${this.agent.name}`
  },
  computed: {
    hasCourse: function () {
      return this.courses.length > 0
    }
  },
  methods: {
    toCourse (courseUrl) {
      let courseUrlComponents = courseUrl.split('/')
      let courseId = parseInt(courseUrlComponents[courseUrlComponents.length - 1])
      this.$router.push({
        path: `/course/${courseId}`
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.courses {
  width: 100%;
  min-height: calc(100vh - 90px);
  background: $back-color;
  padding-top: 50px;
  .empty {
    padding-top: 200px;
    font-size: 36px;
    line-height: 57px;
    font-weight: 700;
    color: $font-tip;
    text-align: center;
  }
  &-container {
    margin-top: 40px;
  }
}
.course-panel {
  cursor: pointer;
  margin-bottom: 24px;
  h3 {
    font-size: 20px;
    margin-bottom: 12px;
  }
  .course-title {
    height: 47px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  .comments {
    color: $font-tip;
    display: flex;
    line-height: 24px;
    margin-bottom: 6px;
    div {
      flex-grow: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    div.precentage {
      width: 40px;
      flex-grow: 0;
      text-align: right;
    }
  }
}
</style>



