<template>
  <div id="app">
    <jsk-header v-if="hasHeader" class="header" header-background-color="#4CB844">
      <sk-header />
    </jsk-header>
    <router-view />
  </div>
</template>

<script>
import SkHeader from './components/Header'
import {
  Header,
  Notification
} from 'jellies'
export default {
  name: 'Homepage',
  components: {
    SkHeader,
    JskHeader: Header
  },
  data: function () {
    return {
      agent: {
        name: '编程学习平台',
        logo: '../../static/logo.svg',
        prefix: ''
      }
    }
  },
  created: function () {
    this.$store.dispatch('getAgent', this.prefix).then(
      ({ data }) => {
        this.agent.prefix = data.prefix
        this.agent.name = data.name
        if (data.logo) {
          this.agent.logo = data.logo
        }
      }
    ).catch(
      () => {
        window.location.replace('//www.suanfaedu.com')
      }
    )
  },
  mounted() {
    window.addEventListener('message', ({ data }) => {
      if (this.$route.path !== '/' && (data === 'Unauthorized' || data === 'notSignedIn')) {
        Notification({
          title: '错误',
          message: '身份验证出错，请重新登录。',
          duration: 3000,
          showClose: false
        })
        this.$router.push({
          path: '/'
        })
      }
    })
  },
  provide: function () {
    return {
      agent: this.agent
    }
  },
  computed: {
    hasHeader: function () {
      if (this.$route.name === 'homepage' || this.$route.name === 'course') {
        return false
      }
      return true
    },
    prefix: function () {
      return window.location.host.split('.')[0]
    }
  }
}
</script>
<style lang="scss">
@import './assets/common.scss';
body {
  background: #F4F5F8;
}
.header {
  position: fixed;
  top: 0;
  z-index: 100;
}
#app {
  font-family: "Segoe UI", "Lucida Grande", Helvetica, Arial, "Microsoft YaHei", FreeSans, Arimo, "Droid Sans","wenquanyi micro hei","Hiragino Sans GB", "Hiragino Sans GB W3", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



